import * as React from 'react';
import * as s from "./Timer.module.scss";

/**
 * Timer - –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –æ—Ç—Å—á–∏—Ç—ã–≤–∞—é—â–∏–π –≤—Ä–µ–º—è –≤ —Ñ–æ—Ä–º–∞—Ç–µ hh:mm:ss / mm:ss / ss
 * –ù–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω—ã–µ props:
 * value: number - –≤—Ä–µ–º—è –≤ —Å–µ–∫—É–Ω–¥–∞—Ö, —Ü–µ–ª–æ–µ —á–∏—Å–ª–æ–≤–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ
 * onChange: function - —Ñ—É–Ω–∫—Ü–∏—è, –∫–æ—Ç–æ—Ä–∞—è –±—É–¥–µ—Ç –≤—ã–∑—ã–≤–∞—Ç—å—Å—è —Å –∏–Ω—Ç–µ—Ä–≤–∞–ª–æ–º –≤ 1 —Å–µ–∫—É–Ω–¥—É
 * stop: boolean - –µ—Å–ª–∏ true, —Ç–æ –≤—ã–∑–æ–≤ —Ñ—É–Ω–∫—Ü–∏–∏ —Å –∏–Ω—Ç–µ—Ä–≤–∞–ª–æ–º –æ—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç—Å—è, –µ—Å–ª–∏ false - –≤–æ–∑–æ–±–Ω–æ–≤–ª—è–µ—Ç—Å—è
 * label: boolean - –µ—Å–ª–∏ false - –Ω–µ –±—É–¥–µ—Ç –ø–æ–∫–∞–∑—ã–≤–∞—Ç—å—Å—è "–í—Ä–µ–º—è: "
 */

export class Timer extends React.Component {
  timer;
  timerWasStoped = false;

  constructor(props) {
    super(props);
    this.state = {
      time: 0,
    }
  }

  componentDidMount() {
    this.setTimer();
  }

  shouldComponentUpdate(nextProps, nextState) {
    if (nextProps.stop) {
      clearInterval(this.timer);
      return false;
    }
    if (nextProps.stop === false && this.props.stop === true) {
      this.setTimer();
    }
    return true;
  }

  clearTimer = () => {
    clearInterval(this.timer);
    this.setState({ time: 0 })
  }

  setTimer = () => {
    if (this.props.onChange && typeof this.props.onChange === "function") {
      this.timer = setInterval(this.props.onChange, 1000);
      return;
    }
    this.timer = setInterval(() => this.setState((state) => {
      let time = parseInt(state.time);
      return { time: time + 1 }
    }), 1000)
  }

  getTime = (value) => {
    let seconds, minutes, hours;
    seconds = value;
    if (value >= 60) {
      minutes = parseInt(value / 60);
      seconds = value - (minutes * 60);
    }
    if (minutes >= 60) {
      hours = parseInt(minutes / 60);
      minutes = minutes - (hours * 60);
    }

    let str;
    if (hours) {
      str = `${hours}:${minutes > 9 ? minutes : '0' + minutes}:${seconds > 9 ? seconds : '0' + seconds}`
    } else if (minutes) {
      str = `${minutes}:${seconds > 9 ? seconds : '0' + seconds}`
    } else {
      str = `${seconds}`
    }
    return str;
  }

  render() {
    return (
      <div className={s.timer}>
        {this.props.label ? "–í—Ä–µ–º—è:" : null}
        {this.getTime(this.props.value ? this.props.value : this.state.time)}
      </div>
    )
  }
}