/**
 * –§–æ—Ä–º–∞—Ç –¥–ª—è –æ–±—ä–µ–∫—Ç–∞ –∫—Ä–µ—Å—Ç–∏–∫–∏-–Ω–æ–ª–∏–∫–∏
 * [[{name: "cross"|"zero"|null, 
 *    winner: boolean
 *   },{...},{...}],
 *  [{...},{...},{...}],
 *  [{...},{...},{...}]
 * ]
 */

export const Translation = {
  cross: "–∫—Ä–µ—Å—Ç–∏–∫",
  zero: "–Ω–æ–ª–∏–∫"
}

export class TictactoeStore {
  size = 3;
  gameStarted = false;
  whoWalkNow = null;
  mode = "alone";

  /**
   * –§–æ—Ä–º–∏—Ä—É–µ—Ç –Ω–æ–≤—É—é data —Å –ø—É—Å—Ç—ã–º–∏ –∫–ª–µ—Ç–∫–∞–º–∏,
   * –æ—Å–Ω–æ–≤—ã–≤–∞—è—Å—å –Ω–∞ —Ä–∞–∑–º–µ—Ä–µ –ø–æ–ª—è size 
   */
  getNullData = () => {
    let data = [];
    if (!this.size) return;
    for (let i = 0; i < this.size; i++) {
      data[i] = [];
      for (let j = 0; j < this.size; j++) {
        data[i][j] = {};
      }
    }
    return data;
  }

  /** –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤ —Å —É—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–Ω—ã–º –∫—Ä–µ—Å—Ç–∏–∫–æ–º –∏–ª–∏ –Ω–æ–ª–∏–∫–æ–º, 
   *  –æ—Å–Ω–æ–≤—ã–≤–∞—è—Å—å –Ω–∞ —Ç–æ–º, –∫—Ç–æ —Ö–æ–¥–∏–ª –¥–æ —ç—Ç–æ–≥–æ  */
  handlerStroke = (rIndex, cIndex, data, mode) => {
    let newData = deepClone(data);
    if (!this.gameStarted) {
      this.gameStarted = true;
      this.whoWalkNow = 'cross';
      if (mode === "bot") {
        this.mode = "bot"
      } else {
        this.mode = "alone"
      }
    } else {
      this.whoWalkNow = this.whoWalkNow === 'cross' ? 'zero' : 'cross';
    }
    newData[rIndex][cIndex].name = this.whoWalkNow;
    return newData;
  }

  handlerStrokeBot = (data) => {
    let newData = deepClone(data);
    let coord = this.botMove(newData);
    if (coord) {
      let [rI, lI] = coord;
      this.whoWalkNow = 'zero';
      newData[rI][lI].name = this.whoWalkNow;
      return newData;
    }
    return data;
  }

  botMove = (data) => {
    /* –ø–æ–∏—Å–∫ –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç—ã, –≤ –∫–æ—Ç–æ—Ä–æ–π –±–æ—Ç (–Ω–æ–ª–∏–∫) –º–æ–∂–µ—Ç –ø–æ–±–µ–¥–∏—Ç—å */
    let coord = this.coordForWin(data, "zero");
    if (coord) return coord;
    /* –ø–æ–∏—Å–∫ –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç—ã, –≤ –∫–æ—Ç–æ—Ä–æ–π –∏–≥—Ä–æ–∫ (–∫—Ä–µ—Å—Ç–∏–∫) –º–æ–∂–µ—Ç –ø—Ä–æ–∏–≥—Ä–∞—Ç—å */
    coord = this.coordForWin(data, "cross");
    if (coord) return coord;
    /* –ø–æ–∏—Å–∫ –ø—É—Å—Ç–æ–π –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç—ã –Ω–∞ —Ç–æ–π –∂–µ –¥–∏–∞–≥–æ–Ω–∞–ª–∏, —Å—Ç—Ä–æ–∫–µ, —Ä—è–¥—É, –≤ –∫–æ—Ç–æ—Ä—É—é —Å—Ö–æ–¥–∏–ª –∏–≥—Ä–æ–∫ */
    coord = this.coordForNextMove(data, "cross");
    if (coord) return coord;
    coord = this.botMoveRandom(data);
    return coord;
  }

  /**
   * –ò—â–µ–º –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç—ã –¥–ª—è –ø–æ–±–µ–¥—ã, –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –º–∞—Å—Å–∏–≤ –∏–∑ –¥–≤—É—Ö –∑–Ω–∞—á–µ–Ω–∏–π
   * [rigthIndex, leftIndex] –µ—Å–ª–∏ –µ—Å—Ç—å –≤–æ–∑–º–æ–∂–Ω—ã–π —Ö–æ–¥, –∏–ª–∏ null, –µ—Å–ª–∏ –Ω–µ—Ç
   */
  coordForWin = (data, name) => {
    let emptyCellsCount = 1;
    let found = this.searchInFirstDiaronal(data, name, emptyCellsCount);
    if (found) return found;
    found = this.searchInSecondDiaronal(data, name, emptyCellsCount);
    if (found) return found;
    found = this.rowSearch(data, name, emptyCellsCount);
    if (found) return found;
    found = this.columnSearch(data, name, emptyCellsCount);
    return found;
  }
  /**
   * –ò—â–µ–º —Ä–∞–Ω–¥–æ–º–Ω—É—é –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç—É –¥–ª—è —Ö–æ–¥–∞ –Ω–∞ —Ç–æ—Ç –∂–µ —Ä—è–¥, —Å—Ç—Ä–æ–∫—É –∏–ª–∏ –¥–∏–∞–≥–æ–Ω–∞–ª—å,
   * –Ω–∞ –∫–æ—Ç–æ—Ä—É—é —Å—Ö–æ–¥–∏–ª –∏–≥—Ä–æ–∫. –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç—Å—è [rigthIndex, leftIndex],
   * –µ—Å–ª–∏ –µ—Å—Ç—å –≤–æ–∑–º–æ–∂–Ω—ã–π —Ö–æ–¥, –∏–ª–∏ null, –µ—Å–ª–∏ –Ω–µ—Ç
   */
  coordForNextMove = (data, name) => {
    let emptyCellsCount = 2;
    let emptyCells = [];
    let found = this.searchInFirstDiaronal(data, name, emptyCellsCount);
    if (found) emptyCells = [...emptyCells, ...found];
    found = this.searchInSecondDiaronal(data, name, emptyCellsCount);
    if (found) emptyCells = [...emptyCells, ...found];
    found = this.rowSearch(data, name, emptyCellsCount);
    if (found) emptyCells = [...emptyCells, ...found];
    found = this.columnSearch(data, name, emptyCellsCount);
    if (found) emptyCells = [...emptyCells, ...found];
    return this.searchRandomItemFromArray(emptyCells);
  }
  /**
   * –ü—Ä–æ–≤–µ—Ä—è–µ–º –¥–∏–∞–≥–æ–Ω–∞–ª—å —Å–ª–µ–≤–∞ –Ω–∞–ø—Ä–∞–≤–æ 
   * */
  searchInSecondDiaronal = (data, name, emptyCellsCount) => {
    let emptyCells = [];
    for (let i = 0; i < data.length; i++) {
      let elemName = data[data.length - 1 - i][i].name;
      if (elemName && elemName !== name) return null;
      if (!elemName) emptyCells.push([data.length - 1 - i,i]);
    }
    if (emptyCells.length === emptyCellsCount) {
      return emptyCells.length > 1 ? emptyCells :emptyCells[0];
    }
    return null;
  }
  /**
   * –ü—Ä–æ–≤–µ—Ä—è–µ–º –¥–∏–∞–≥–æ–Ω–∞–ª—å —Å–ø—Ä–∞–≤–∞ –Ω–∞–ª–µ–≤–æ 
   * */
  searchInFirstDiaronal = (data, name, emptyCellsCount) => {
    let emptyCells = [];
    for (let i = 0; i < data.length; i++) {
      let elemName = data[i][i].name;      
      if (elemName && elemName !== name) return null;
      if (!elemName) emptyCells.push([i,i]);
    }
    if (emptyCells.length === emptyCellsCount) {
      return emptyCells.length > 1 ? emptyCells :emptyCells[0];
    }
    return null;
  }

  rowSearch = (data, name, emptyCellsCount) => {
    for (let i = 0; i < data.length; i++) {
      let emptyCells = [];
      for (let j = 0; j < data[i].length; j++) {
        let elemName = data[i][j].name;
        if (elemName && elemName !== name) {
          emptyCells = [];
          break;
        }
        if (!elemName) emptyCells.push([i,j]); 
      }
      if (emptyCells.length === emptyCellsCount) {
        return emptyCells.length > 1 ? emptyCells :emptyCells[0];
      }
    }
    return null;
  }

  columnSearch = (data, name, emptyCellsCount) => {
    for (let i = 0; i < data.length; i++) {
      let emptyCells = [];
      for (let j = 0; j < data[i].length; j++) {
        let elemName = data[j][i].name;
        if (elemName && elemName !== name) {
          emptyCells = [];
          break;
        }
        if (!elemName) emptyCells.push([j,i]); 
      }
      if (emptyCells.length === emptyCellsCount) {
        return emptyCells.length > 1 ? emptyCells :emptyCells[0];
      }
    }
    return null;
  }

  searchRandomItemFromArray = (arr) => {
    if (!arr) return;
    let randomIndex = Math.floor(Math.random() * (arr.length - 0)) + 0;
    return arr[randomIndex];
  }

  botMoveRandom = (data) => {
    let emptyCells = [];
    for (let i = 0; i < data.length; i++) {
      let row = data[i];
      for (let j = 0; j < row.length; j++) {
        if (!row[j].name) emptyCells.push([i, j]);
      }
    }
    return this.searchRandomItemFromArray(emptyCells);
  }

  /**
   * –ü—Ä–æ–≤–µ—Ä–∫–∞ —á—Ç–æ –∑–∞–∫–æ–Ω—á–∏–ª–∏—Å—å —Ö–æ–¥—ã, 
   * –µ—Å–ª–∏ –≤—Å–µ –∫–ª–µ—Ç–∫–∏ –∑–∞–Ω—è—Ç—ã, –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç true, 
   * –µ—Å–ª–∏ –µ—Å—Ç—å –ø—É—Å—Ç—ã–µ –∫–ª–µ—Ç–∫–∏, –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç false
   */
  movesOver = (data) => {
    if (!data.length) return false;
    for (let i = 0; i < data.length; i++) {
      for (let j = 0; j < data.length; j++) {
        if (!data[i][j].name) {
          return false;
        };
      }
    }
    return true;
  }

  /**
   * –ù–∞—Ö–æ–¥–∏—Ç –ø–æ–±–µ–¥–∏—Ç–µ–ª—è, –µ—Å–ª–∏ –Ω–µ –Ω–∞—Ö–æ–¥–∏—Ç - –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç null,
   * –ï—Å–ª–∏ –Ω–∞—Ö–æ–¥–∏—Ç - –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç name(–µ–≥–æ –∏–º—è)
   */
  getWinner = (data) => {
    if (!data.length) return null;
    for (let i = 0; i < data.length; i++) {
      for (let j = 0; j < data.length; j++) {
        if (data[i][j].winner) {
          return data[i][j].name;
        };
      }
    }
    return null;
  }
  
  /**
   * –í—ã–ø–æ–ª–Ω—è–µ—Ç –ø—Ä–æ–≤–µ—Ä–∫–∏ –Ω–∞ –ø–æ–±–µ–¥—É 
   * –∏ —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç –∫–ª–µ—Ç–∫–∞–º —Å–≤–æ–π—Å—Ç–≤–æ winner –≤ true,
   * –µ—Å–ª–∏ –æ–Ω–∏ –Ω–∞—Ö–æ–¥—è—Ç—Å—è –≤ —Ä—è–¥—É –ø–æ–±–µ–¥–∏—Ç–µ–ª—è
   */
  setWinnerStyle = (data) => {
    let newData = deepClone(data);
    if (!newData.length) return newData;
    let isWinner = false;

    /** –ó–∞–ø–æ–ª–Ω–µ–Ω–∞ –¥–∏–∞–≥–æ–Ω–∞–ª—å —Å–ª–µ–≤–∞ –Ω–∞–ø—Ä–∞–≤–æ */
    isWinner = this.checkLeftDiagonal(newData);
    if (isWinner) {
      for (let i = 0; i < newData.length; i++) {
        newData[i][i].winner = true;
      }
      return newData;
    }

    /** –ó–∞–ø–æ–ª–Ω–µ–Ω –¥–∏–∞–≥–æ–Ω–∞–ª—å —Å–ø—Ä–∞–≤–∞ –Ω–∞–ª–µ–≤–æ */
    isWinner = this.checkRightDiagonal(newData);
    if (isWinner) {
      for (let i = 0; i < newData.length; i++) {
        newData[data.length - 1 - i][i].winner = true;
      }
      return newData;
    }

    /** –ó–∞–ø–æ–ª–Ω–µ–Ω –æ–¥–∏–Ω –∏–∑ —Ä—è–¥–æ–≤ –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª–∏ */
    for (let i = 0; i < newData.length; i++) {
      isWinner = this.checkRow(newData[i]);
      if (isWinner) {
        for (let j = 0; j < newData[i].length; j++) {
          newData[i][j].winner = true;
        }
        return newData;
      }
    }

    /** –ó–∞–ø–æ–ª–Ω–µ–Ω –æ–¥–∏–Ω –∏–∑ —Ä—è–¥–æ–≤ –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª–∏ */
    let columnIndex = this.findWinner–°olumnIndex(newData);
    isWinner = columnIndex !== -1 ? true : false;
    if (isWinner) {
      for (let i = 0; i < newData.length; i++) {
        newData[i][columnIndex].winner = true;
      }
      return newData;
    }
    return newData;
  }

  checkRow = (data) => {
    if (!data.length) return false;
    let firstElem = data[0].name;
    if (!firstElem) return false;
    for (let i = 0; i < data.length; i++) {
      if (data[i].name !== firstElem) return false;
    }
    return true;
  }

  checkLeftDiagonal = (data) => {
    let firstElem = data[0][0].name;
    if (!firstElem) return false;
    for (let i = 0; i < data.length; i++) {
      if (data[i][i].name !== firstElem) return false;
    }
    return true;
  }

  checkRightDiagonal = (data) => {
    let firstElem = data[data.length - 1][0].name;
    if (!firstElem) return false;
    for (let i = 0; i < data.length; i++) {
      if (data[data.length - 1 - i][i].name !== firstElem) return false;
    }
    return true;
  }

  findWinner–°olumnIndex = (data) => {
    if (!data.length) return -1;

    for (let i = 0; i < data.length; i++) {
      let firstElem = data[0][i].name;
      if (!firstElem) continue;
      let found = true;
      for (let j = 0; j < data.length; j++) {
        if (data[j][i].name !== firstElem) {
          found = false;
          break;
        }
      }
      if (found) {
        return i;
      }
    }
    return -1;
  }

}

export function deepClone(obj) {
  if (obj === null || typeof obj !== "object") {
    return obj;
  }

  if (obj instanceof Array && obj.length) {
    let copy = [];
    for (let i = 0; i < obj.length; i++) {
      copy[i] = deepClone(obj[i]);
    }
    return copy;
  }

  if (obj instanceof Object) {
    let copy = {};
    for (let key in obj) {
      if (obj.hasOwnProperty(key)) {
        copy[key] = deepClone(obj[key]);
      }
    }
    return copy;
  }
}
